$xs: 576;
$sm: 768;
$md: 992;
$lg: 1200;

// colors
$firstColor: #DFBC82;

html {
    font-size: 14px;
    @include rmin($sm) {
        font-size: 15px; }
    @include rmin($md) {
        font-size: 16px; } }
body {
	font-family: 'Roboto', sans-serif;
	font-weight: 300;
	font-size: 1rem;
	line-height: 1.4 {
    background: #ccc; } }

// General
img {
	max-width: 100%;
	font-size: 0;
	line-height: 0;
	display: block; }

a {
	text-decoration: none;
	&:hover {
		text-decoration: none; } }

p {
	margin-bottom: 1rem; }

section {
    padding-top: 2rem;
    padding-bottom: 2rem;
    @include rmin($sm) {
        padding-top: 3rem;
        padding-bottom: 3rem; } }

.mt-0 {
    margin-top: 0 !important; }
.mb-0 {
    margin-bottom: 0 !important; }
.pt-0 {
    padding-top: 0 !important; }



.container {
    width: 100%;
    max-width: 540px;
    margin: 0 auto;
    padding: 0 20px;
    @include rmin($sm) {
        max-width: 720px; }
    @include rmin($md) {
        max-width: 960px; }
    @include rmin($lg) {
        max-width: 1170px; } }


.title {
    font-family: 'Ubuntu';
    font-size: 1.75rem;
    color: #000000;
    text-align: center;
    margin-bottom: 2rem;
    font-weight: 400;
    @include rmin($sm) {
        font-size: 2.5rem;
        margin-bottom: 3rem; }
    &--white {
        color: #ffffff; }
    &--order {
        font-size: 1.25rem;
        margin-bottom: 1.5rem;
        @include rmin($sm) {
            font-size: 1.5rem; } } }

.subtitle {
    font-family: 'Roboto Condensed';
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 1.5rem;
    &--center {
        text-align: center; } }

// BTN
.btn {
    display: inline-block;
    padding: 0.75rem 1rem;
    max-width: 15rem;
    width: 100%;
    color: #000000;
    text-align: center;
    font-family: 'Ubuntu';
    font-size: 1rem;
    background: $firstColor;
    border: 1px solid $firstColor;
    display: block;
    @include tr(all ease 0.2s);
    &:hover {
        background: #ffffff; }
    &--center {
        margin: 0 auto; }
    &--mcenter {
        margin: 0 auto;
        @include rmin($xs) {
            margin: 0; } }
    &--mb {
        margin-bottom: 1rem !important; }
    &--revers {
        background: #ffffff;
        &:hover {
            background: $firstColor; } }
    &__group {
        display: flex;
        flex-wrap: wrap;
        //justify-content: space-between
        margin-top: 2rem;
        & .btn {
            width: calc(50% - 1rem);
            margin-right: 1rem;
            &:last-child {
                margin-right: 0; } } } }

// form
.form {
    &__response {
        display: none;
        width: 100%;
        text-align: center;
        margin-top: 1rem;
        color: brown;
        font-weight: 700; }
    &__group {
        text-align: center;
        &--col {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            max-width: 56rem;
            margin: 2rem auto 0; } }
    &__input {
        border-top: 0;
        border-left: 0;
        border-right: 0;
        border-bottom: 2px solid #cccccc;
        width: 100%;
        font-size: 1rem;
        color: #333333;
        padding: 0.25rem 0;
        margin-bottom: 1.5rem;
        text-align: center;
        background: transparent;
        @include tr(all ease 0.2s);
        @include rmin($xs) {
            margin-bottom: 0;
            text-align: left; }
        &:focus {
            border-bottom: 2px solid $firstColor; }
        &.error {
            border-bottom: 2px solid brown; }
        &--col {
            width: 100%;
            @include rmin($xs) {
                width: calc(33.33% - 1rem); } }
        &--white {
            color: #ffffff;
            &::placeholder {
                color: #cccccc; } } }

    &__file input {
        height: 0;
        opacity: 0;
        overflow: hidden;
        margin: 0;
        padding: 0;
        display: none; }
    &__file img {
        max-width: 1.375rem;
        margin-right: 0.5rem; }
    &__file-fake {
        display: flex;
        justify-content: center;
        cursor: pointer; }
    &__file-fake span {
        border-bottom: 1px solid $firstColor;
        font-size: 0.875rem; } }

// table
.table-overflow {
    width: 100%;
    overflow-x: scroll; }
.table {
    width: 100%;
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-family: 'Roboto';
    font-size: 1rem;
    & thead {
        background: rgba(223,188,130,.3);
        & th {
            padding: 1rem;
            text-align: left; }
        & tr {
            border: none; } }
    & td {
        padding: 1rem;
        text-align: left; }
    & tr {
        border-bottom: 1px solid #aaaaaa; }
    &__order {
        color: #000000;
        border-bottom: 1px solid $firstColor;
        @include tr(all ease 0.2s);
        &:hover {
            border-bottom: 1px solid transparent;
            color: $firstColor; } } }


// tabs
.tabs {
    list-style: none;
    width: 100%;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    &__lists {
        margin-bottom: 0 !important;
        &:nth-child(2n) {
            & a {
                background: #ffffff !important; } } }
    & li {
        list-style: none;
        padding: 0;
        margin-left: 0 !important;
        padding-left: 0 !important;
        &:after {
            display: none; } }
    &__link {
        padding: 1rem;
        font-weight: 700;
        background: rgba(223,188,130,.3);
        width: 100%;
        display: block;
        color: #000000;
        position: relative;
        &:after {
            content: ' ';
            position: absolute;
            width: 1.5rem;
            height: 1.5rem;
            top: 0.75rem;
            right: 2rem;
            background-image: url(../img/icon/arr.svg);
            background-size: cover;
            transform: rotate(-90deg);
            @include tr(all ease 0.2s); }
        &--is-open {
            &:after {
                transform: rotate(90deg);
                top: 1.25rem; } } }
    &__item {
        height: 0;
        overflow: hidden;
        @include tr(all ease 0.3s); } }


// filter
.filter {
    padding: 1.5rem;
    margin-bottom: 2rem;
    max-width: 540px;
    margin: 0 auto;
    @include rmin($sm) {
        border: 2px solid $firstColor;
        max-width: 720px; }
    &__block {
        margin-bottom: 2rem; }
    &__title {
        font-size: 1.25rem;
        font-family: 'Roboto';
        font-weight: 700;
        margin-bottom: 0.75rem;
        position: relative;
        @include rmin($md) {
            padding-left: 1.5rem;
            &:after {
                content: ' ';
                width: 1rem;
                height: 2px;
                background: $firstColor;
                position: absolute;
                top: calc(50% - 1px);
                left: 0; } } }
    &__checkbox {
        border: 1px solid $firstColor;
        width: 1.5rem;
        height: 1.5rem;
        margin-right: 0.5rem;
        display: none;
        @include rmin($sm) {
            width: 1rem;
            height: 1rem; } }
    &__checkbox-label {
        padding-left: 2rem;
        position: relative;
        margin-bottom: 1rem;
        display: block;
        font-size: 1.125rem;
        @include rmin($sm) {
            padding-left: 1.5rem;
            margin-bottom: 0.5rem;
            font-size: 1rem; }
        &:after {
            content: ' ';
            position: absolute;
            left: 0;
            bottom: 0.25rem;
            width: 1.25rem;
            height: 1.25rem;
            border: 2px solid $firstColor;
            @include rmin($sm) {
                width: 1rem;
                height: 1rem; } } }
    &__checkbox:checked + &__checkbox-label:before {
        content: ' ';
        position: absolute;
        left: 4px;
        bottom: calc(0.25rem + 4px);
        width: calc(1.5rem - 12px);
        height: calc(1.5rem - 12px);
        background: $firstColor;
        @include rmin($sm) {
            width: calc(1rem - 8px);
            height: calc(1rem - 8px); } } }



// remodal
.remodal {
    padding: 0;
    max-width: 500px !important;
    @include rmin($md) {
        max-width: 900px !important; }
    &__wrap {
        display: flex;
        flex-wrap: wrap; }
    &__left {
        width: 100%;
        @include rmin($md) {
            width: 50%; }
        & img {
            margin: 0;
            padding: 0; } }
    &__img {
        position: relative;
        &-overflow {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-content: center;
            align-items: center; }
        &-block {
            border-bottom: 2px solid $firstColor; }
        &-title {
            font-size: 1.375rem;
            font-weight: 700;
            font-family: 'Roboto Condensed';
            color: #ffffff;
            margin-bottom: 1rem; } }
    &__right {
        padding: 2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        align-content: center;
        flex-wrap: wrap;
        width: 100%;
        @include rmin($md) {
            width: 50%; } }
    &__title {
        font-size: 1.25rem;
        font-family: 'Roboto Condensed';
        font-weight: 700;
        text-align: center;
        margin-bottom: 1rem;
        width: 100%; }
    &__subtitle {
        text-align: center;
        width: 100%; }
    &__form {
        max-width: 18rem;
        margin-top: 1rem;
        text-align: center;
        & input {
            width: 100%;
            margin-bottom: 1rem;
            text-align: center;
            font-size: 0.875rem; }
        & button {
            margin-top: 1rem; } }
    &-close {
        right: 0;
        left: auto;
        &:before {
            color: #ffffff;
            z-index: 2;
            font-size: 3rem;
            line-height: 3rem;
            @include rmin($md) {
                color: #000000;
                font-size: 2.5rem;
                line-height: 2.5rem; } } } }


// UI slider
.mse2_number_slider {
    height: 4px;
    & .ui-slider-range {
        background: $firstColor; } }

.filter-number {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    &__col {
        width: calc(50% - 0.5rem);
        & label {
            width: 100%; }
        & input[type="text"] {
            padding: 0.5rem 0.75rem;
            border: 1px solid $firstColor;
            max-width: 100%; } } }
