
// top-line
.top-line {
	background: #21191D;
	&__wrap {
		display: flex;
		justify-content: space-between;
		padding: 0.5rem 0;
		align-content: center;
		align-items: center; }
	&__burger {
		display: flex;
		align-items: center;
		@include rmin($md) {
			display: none; }
		&-link {
			display: inline-block;
			position: relative;
			& span {
				height: 1px;
				width: 31px;
				display: block;
				margin: 0.425rem 0;
				background: $firstColor;
				@include tr(all ease 0.2s); }
			&.is-open {
				& span {
					margin: 0;
					&:first-child {
						transform: rotate(45deg); }
					&:last-child {
						transform: rotate(-45deg);
						position: absolute;
						bottom: 0; }
					&:nth-child(2) {
						opacity: 0; } } } } }
	&__logo {
		display: flex;
		align-items: center;
		align-content: center;
		@include rmin($md) {
			display: none; }
		& img {
			width: 3.5rem;
			margin-right: 1rem; }
		& span {
			color: #ffffff;
			font-weight: 700; } }
	&__mphone {
		display: inline-block;
		@include rmin($md) {
			display: none; }
		& img {
			width: 1.5rem; } }
	&__contact {
		color: #ffffff;
		align-content: center;
		align-items: center;
		display: none;
		@include rmin($md) {
			display: flex; } }
	&__address {
		color: rgba(255, 255, 255, 0.87); }
	&__phone {
		font-size: 1.125rem;
		margin-left: 3rem;
		padding-left: 2rem;
		font-weight: 900;
		position: relative;
		&:after {
			position: absolute;
			left: 0;
			top: 0.125rem;
			content: ' ';
			width: 1.25rem;
			height: 1.25rem;
			background-image: url(../img/icon/phone.svg);
			background-repeat: no-repeat;
			background-size: contain; } }
	&__country {
		display: none;
		position: relative;
		@include rmin($md) {
			display: block; } }
	&__country-href {
		color: rgba(255, 255, 255, 0.87);
		position: relative;
		padding-left: 2rem;
		padding-right: 1rem;
		font-size: 1.125rem;
		cursor: pointer;
		@include rmin($md) {
			margin-left: 4.25rem;
			font-size: 1rem;
			padding-left: 1rem; }
		&:after {
			position: absolute;
			left: 0;
			content: ' ';
			width: 1.25rem;
			height: 1.25rem;
			background-image: url(../img/icon/city-select.svg);
			background-position: center center;
			background-repeat: no-repeat;
			background-size: contain;
			@include rmin($md) {
				width: 0.75rem;
				height: 1rem; } }
		&:before {
			position: absolute;
			right: 0;
			bottom: 0;
			content: ' ';
			width: 0.75rem;
			height: 0.75rem;
			background-image: url(../img/icon/city-select-arr.svg);
			background-repeat: no-repeat;
			background-size: contain; } }
	&__country {
		&--show {
			width: 100%;
			display: block;
			margin: 1rem 0;
			@include rmin($md) {
				display: none; } }
		&-list {
			display: none;
			padding: 0.25rem 1rem;
			background: #fafafa;
			box-shadow: 0 0 5px 2px rgba(0,0,0,.1);
			margin-top: 1rem;
			@include rmin($md) {
				position: absolute;
				z-index: 4;
				left: 4.25rem;
				top: 2rem;
				margin-top: 0; } }
		&-item {
			margin: 0.25rem 0; }
		&-link {
			color: #000000;
			@include tr(all ease 0.2s);
			&:hover {
				color: $firstColor; } }
		&.is-open &-list {
			display: block; }
		&.is-open &-href {
			&:before {
				transform: rotate(180deg);
				top: 0.25rem; } } } }


// navigation
.navigation {
	background: #2E2423;
	padding: 0.5rem 0;
	display: block;
	position: fixed;
	z-index: 3;
	width: 100%;
	height: 100%;
	overflow-y: scroll;
	padding-bottom: 6rem;
	left: calc( -100% - 1rem );
	@include tr(all ease 0.3s);
	@include rmin($md) {
		display: block;
		position: relative;
		left: 0;
		height: auto;
		overflow-y: visible;
		padding-bottom: 0.5rem; }
	& * {
		list-style: none; }
	&.is-open {
		transform: translateX(calc(100% + 1rem));
		background: #2E2423;
		@include rmin($md) {
			transform: none; } }
	&--main {
		background: transparent; }
	&__wrap {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between; }
	&__logo {
		width: 100%;
		max-width: 14rem;
		display: none;
		@include rmin($md) {
			display: block; }
		& a {
			display: flex;
			align-content: center;
			align-items: center; }
		& img {
			width: 4rem;
			margin-right: 0.75rem; }
		& span {
			font-weight: 700;
			color: #ffffff; } }
	&__menu {
		display: flex;
		justify-content: space-between;
		align-items: center;
		@include rmin($md) {
			width: calc(100% - 17rem); } }
	&__list {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		padding: 0.5rem 0;
		width: 100%;
		justify-content: space-between;
		position: relative;
		@include rmin($md) {
			padding: 0;
			flex-wrap: nowrap; } }
	&__item {
		padding: 0.5rem 0;
		//position: relative
		width: 100%;
		@include rmin($md) {
			width: auto;
			padding: 0.25rem 0;
			margin-left: 0.5rem; }
		@include rmin($lg) {
			margin-left: 1.5rem; } }
	&__item:hover &__submenu {
		display: block; }
	&__item:hover .sub {
		display: block !important; }
	&__link {
		color: #ffffff;
		position: relative;
		font-family: 'Roboto Condensed';
		font-weight: 700;
		font-size: 1.125rem;
		@include rmin($md) {
			font-family: 'Roboto';
			font-weight: 300;
			font-size: 1rem;
			&:after {
				content: ' ';
				width: 0;
				position: absolute;
				left: 0;
				bottom: -3px;
				height: 1px;
				background: #ffffff;
				@include tr(all ease 0.3s); }
			&:hover:after {
				width: 100%; } } }
	&__slink {
		padding-right: 1rem;
		&:before {
			position: absolute;
			right: 0;
			bottom: 0.125rem;
			content: ' ';
			width: 0.75rem;
			height: 0.75rem;
			background-image: url(../img/icon/city-select-arr.svg);
			background-repeat: no-repeat;
			background-size: contain; } }
	&__submenu {
		background: #312323;
		padding: 0 0 0rem 0;
		display: none;
		//min-width: 20rem
		left: -1.5rem;
		@include rmin($md) {
			position: absolute;
			padding: 1rem 0; }
		&.is-opened {
			display: block; }
		& li {
			padding: 0.125rem 0;
			margin: 0.25rem 1rem;
			& a {
				font-weight: 300;
				@include rmin($md) {
					white-space: nowrap; } } } }
	&__shadow {
		box-shadow: 0 0 3px 3px rgba(0,0,0,.1);
		background: #2E2423; }

	&__phone {
		font-size: 1.125rem;
		padding-left: 2rem;
		font-weight: 900;
		position: relative;
		color: #ffffff;
		margin: 1rem 0;
		width: 100%;
		@include rmin($md) {
			display: none; }
		&:after {
			position: absolute;
			left: 0;
			top: 0.125rem;
			content: ' ';
			width: 1.25rem;
			height: 1.25rem;
			background-image: url(../img/icon/phone.svg);
			background-repeat: no-repeat;
			background-size: contain; } }
	&__social {
		display: flex;
		margin: 1rem 0;
		@include rmin($md) {
			display: none; }
		&-link {
			width: 2rem;
			height: 2rem;
			margin-right: 1rem;
			display: block; } } }

// Submenu ver 2
.sub {
	padding-top: 1rem;
	width: 100%;
	display: none;
	@include rmin($md) {
		position: absolute;
		left: 0; }
	&__wrap {
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		justify-content: space-between;
		background: #ffffff;
		padding: 1.5rem 2rem;
		box-shadow: 0 0 5px 5px rgba(0,0,0,.1); }
	&__list {
		width: 100%;
		margin-bottom: 2rem;
		@include rmin($sm) {
			width: calc(50% - 2rem); }
		@include rmin($md) {
			width: calc(33.333% - 2rem);
			margin-bottom: 0rem; }
		&:last-child {
			margin-bottom: 0; } }
	&__title {
		font-weight: 700;
		padding: 0.5rem 0;
		position: relative;
		margin-bottom: 1rem;
		&:after {
			content: '';
			left: 0;
			position: absolute;
			width: 3rem;
			height: 2px;
			bottom: 0;
			background: $firstColor; }
		& a {
			color: #000000;
			@include tr(all ease 0.2s);
			&:hover {
				color: $firstColor; } } }
	&__item {
		padding: 0.375rem 0;
		& a {
			color: #000000;
			font-size: 0.925rem;
			@include tr(all ease 0.2s);
			&:hover {
				color: $firstColor; } } } }


// main slider
.slider {
	position: relative;
	min-height: 100vh;
	padding-bottom: 5rem;
	background: #2E2423;
	@include rmin($xs) {
		padding-bottom: 2rem; }
	&__title {
		font-family: Ubuntu;
		font-weight: 500;
		color: #ffffff;
		padding-top: 2rem;
		font-size: 1.675rem;
		line-height: 1.4;
		text-align: center;
		@include rmin($xs) {
			padding-top: 2rem;
			font-size: 3rem;
			line-height: 4rem;
			max-width: 45rem;
			text-align: left; }
		@include rmin($lg) {
			padding-top: calc(50vh - 350px); } }
	&__postitle {
		display: flex;
		justify-content: space-between;
		margin-top: 1.5rem; }
	&__advantages {
		display: flex;
		flex-wrap: wrap;
		max-width: 45rem;
		justify-content: space-between; }
	&__adv {
		position: relative;
		width: 100%;
		display: flex;
		align-items: center;
		@include rmin($xs) {
			width: calc(33% - 2rem);
			display: block;
			&:after {
				position: absolute;
				width: 2px;
				height: 55%;
				right: -1rem;
				top: 23%;
				content: ' ';
				background: $firstColor; } }
		&:last-child:after {
			display: none; }
		&-number {
			font-family: 'Neumann';
			font-size: 4rem;
			color: $firstColor;
			display: flex;
			align-content: center;
			align-items: center;
			flex: 0 0 10rem;
			@include rmin($xs) {
				color: #ffffff; }
			& span {
				font-family: Roboto;
				font-size: 1rem;
				padding-left: 1rem; } }
		&-descr {
			color: #ffffff;
			line-height: 1.6; } }

	&__scroll {
		max-width: 10rem;
		color: #ffffff;
		text-align: center;
		position: relative;
		display: none;
		@include rmin($md) {
			display: block; }
		&:after {
			position: absolute;
			bottom: -1.5rem;
			left: calc(50% - 0.6125rem);
			content: ' ';
			width: 1.25rem;
			height: 1.25rem;
			background-image: url(../img/icon/home-arr.svg);
			background-repeat: no-repeat;
			background-size: contain; }
		&-img {
			width: 4rem;
			margin: 0 auto; }
		&-title {
			font-weight: 700;
			font-family: 'Roboto Condensed';
			font-size: 1.5rem;
			padding: 0.25rem 0; } }
	&__gift {
		margin-top: 2rem;
		margin-bottom: 2rem;
		text-align: center;
		@include rmin($xs) {
			text-align: left; }
		&-title {
			color: #ffffff; }
		&-bold {
			color: #ffffff;
			font-weight: 700;
			font-size: 1.75rem; } }
	&__nav {
		display: flex;
		justify-content: center;
		align-content: center;
		align-items: center;
		position: absolute;
		bottom: 2rem;
		width: 100%; }
	&__arr {
		width: 1.25rem;
		height: 1.25rem;
		background-image: url(../img/icon/home-arr.svg);
		background-repeat: no-repeat;
		background-size: contain;
		cursor: pointer;
		&--left {
			transform: rotate(90deg); }
		&--right {
			transform: rotate(-90deg); } }
	&__dots {
		display: flex;
		align-content: center;
		align-items: center; }
	&__dot {
		width: 5px;
		height: 5px;
		border-radius: 50%;
		background: $firstColor;
		margin: 0 0.5rem;
		cursor: pointer;
		border: 1px solid $firstColor;
		@include tr(all ease 0.3s);
		&--active {
			width: 12px;
			height: 12px;
			border: 1px solid $firstColor;
			background: transparent; } } }


// main-services
.main-services {
	&__wrap {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		&--big {
