// main-production
.main-production {
	margin-top: 1rem;
	background: url(../img/main-production/main-production-bg.jpg);
	background-size: cover;
	&__wrap {
		display: flex;
		width: 100%;
		flex-wrap: wrap;
		@include rmin($md) {
			flex-flow: row nowrap; } }
	&__item {
		overflow: hidden;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		position: relative;
		flex: 1 1 100%;
		height: 8rem;
		margin-bottom: 1rem;
		cursor: pointer;
		@include tr(all ease 0.3s);
		@include rmin($md) {
			flex: 1 1 16.66666667%;
			height: 30rem;
			margin-bottom: 0; }
		&:nth-child(1) {
			background-image: url(../img/main-production/1.jpg); }
		&:nth-child(2) {
			background-image: url(../img/main-production/2.jpg); }
		&:nth-child(3) {
			background-image: url(../img/main-production/3.jpg); }
		&:nth-child(4) {
			background-image: url(../img/main-production/4.jpg); }
		&:nth-child(5) {
			background-image: url(../img/main-production/5.jpg); }
		&:nth-child(6) {
			background-image: url(../img/main-production/6.jpg); } }

	&__block {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		opacity: 1;
		@include tr(all ease 0.3s);
		& img {
			margin-bottom: 1rem;
			max-width: 3.5rem;
			@include rmin($md) {
				max-width: 4rem; } } }
	&__title {
		font-size: 1.25rem;
		font-weight: 700;
		font-family: 'Roboto Condensed';
		color: #ffffff; }
	&__text {
		background: rgba(0,0,0,.7);
		padding: 1.5rem;
		height: 100%;
		color: #ffffff;
		display: flex;
		align-content: center;
		align-items: center;
		opacity: 0;
		visibility: hidden;
		position: relative;
		z-index: 2;
		width: 100%;
		cursor: pointer;
		@include tr(all ease 0.3s);
		@include rmin($md) {
			width: 20rem; } }
	&__item--is-active &__text {
		opacity: 1;
		visibility: visible; }
	&__item {
		&--is-active {
			flex: 0.000000001 0.00000001 100%;
			height: auto;
			@include rmin($md) {
				flex: 0.000000001 0.00000001 20rem; } } }
	&__item--is-active &__block {
		opacity: 0; } }



// main-creating
.main-creating {
	padding-bottom: 0;
	&__wrap {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		background: url(../img/main-creating-bg.jpg);
		background-repeat: no-repeat;
		background-size: 300px;
