.article {
	display: block;
	color: #000000;
	&__img {
		margin-bottom: 0.75rem; }
	&__title {
		font-family: 'Roboto condensed';
		font-size: 1.375rem;
		font-weight: 700;
		margin-bottom: 0.5rem; }
	&__description {
		margin-bottom: 0.5rem; }
	&__date {
		color: #cccccc;
		font-size: 0.875rem; } }



// block-order
.block-order {
	display: none;
	border: 2px solid $firstColor;
	padding: 1.5rem 1.5rem 11rem 1.5rem;
	background-image: url(../img/block-order.png);
	background-position: bottom center;
	background-size: contain;
	background-repeat: no-repeat;
	@include rmin($md) {
		display: block; }
	&__title {
		font-family: 'Roboto Condensed';
		font-size: 1.25rem;
		font-weight: 700;
		text-align: center;
		margin-bottom: 1rem; } }

// project
.project {
	&__top {
		@include rmin($md) {
			display: none; } }
	&__wrap {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between; }
	&__left {
		width: 100%;
		margin-bottom: 1rem;
		@include rmin($md) {
			width: calc(55% - 1rem); } }
	&__right {
		width: 100%;
		@include rmin($md) {
			width: calc(45% - 1rem); } }
	&__title {
		font-size: 2.5rem;
		font-family: 'Ubuntu';
		font-weight: 500;
		padding-bottom: 1rem;
		border-bottom: 1px solid $firstColor;
		display: none;
		@include rmin($md) {
			display: block; } }
	&__slider {
		position: relative; }
	&__thumbs {
		margin-top: 0.75rem;
		padding-left: 2rem;
		padding-right: 2rem;
		position: relative;
		display: none;
		@include rmin($xs) {
			display: block; }
		& .owl-item {
			position: relative; }
		& .current {
			&:after {
				content: ' ';
				position: absolute;
				bottom: 0;
				left: 0;
				height: 5px;
				width: 100%;
				background: $firstColor; } }
		&-arr {
			position: absolute;
			top: calc(50% - 1rem);
			width: 2rem;
			height: 2rem;
			background: url(../img/icon/arr.svg);
			background-position: center center;
			background-repeat: no-repeat;
			background-size: contain;
			cursor: pointer;
			&--left {
				transform: rotate(0deg);
				left: 0; }
			&--right {
				transform: rotate(180deg);
				right: 0; } } } }

// characteristics
.characteristics {
	padding: 0.75rem 0;
	border-bottom: 1px solid $firstColor;
	margin-bottom: 1.5rem;
	&__item {
		display: flex;
		align-content: center;
		align-items: center;
		padding: 0.5rem 0; }
	&__icon {
		width: 1.25rem;
		height: 1.25rem;
		margin-right: 1rem; }
	&__text {
		font-size: 1.125rem;
		font-family: 'Roboto';
		& span {
			font-family: 'Roboto Condensed';
			font-weight: 700; } } }

// blike
.blike {
	padding-top: 0;
	&__block {
		border: 2px solid $firstColor;
		padding-left: 2rem;
		padding-right: 2rem;
		background-image: url(../img/project/blike.png);
		background-position: bottom left 60%;
		background-size: contain;
		background-repeat: no-repeat;
		background-size: 20rem;
		min-height: 15rem;
		padding-top: 1rem;
		@include rmin($md) {
			background-size: contain;
			min-height: 15rem;
			display: flex;
			align-content: center;
			align-items: center;
			width: 100%;
			padding-top: 0; } }
	&__wrap {
		display: flex;
		flex-direction: column;
		align-items: center;
		align-content: center;
		width: 100%;
		min-height: 17rem;
		@include rmin($md) {
			flex-direction: row;
			flex-wrap: wrap;
			justify-content: space-between;
			min-height: auto; } }

	&__title {
		font-size: 1.75rem;
		font-family: 'Ubuntu';
		margin-bottom: 1rem;
		@include rmin($sm) {
			font-size: 2.25rem; }
		@include rmin($md) {
			font-size: 2.5rem;
			margin-bottom: 0; } } }

// inp
.inp {
	padding-top: 1rem;
	&__top-title {
		font-size: 1.5rem;
		font-family: 'Roboto Condensed';
		margin-bottom: 3rem;
		font-weight: 700;
		text-align: center; }
	&__wrap {
		display: flex;
		width: 100%;
		flex-wrap: wrap;
		@include rmin($md) {
			flex-flow: row nowrap; } }
	&__item {
		overflow: hidden;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		position: relative;
		flex: 1 1 100%;
		height: 8rem;
		margin-bottom: 1rem;
		cursor: pointer;
		@include tr(all ease 0.3s);
		@include rmin($md) {
			flex: 1 1 25%;
			height: 30rem;
			margin-bottom: 0; }
		&:nth-child(1) {
			background-image: url(../img/project/inp1.png); }
		&:nth-child(2) {
			background-image: url(../img/project/inp2.png); }
		&:nth-child(3) {
			background-image: url(../img/project/inp3.png); }
		&:nth-child(4) {
			background-image: url(../img/project/inp4.png); } }

	&__block {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		flex-direction: column;
		padding-bottom: 1rem;
		opacity: 1;
		@include tr(all ease 0.3s);
		@include rmin($md) {
			padding-bottom: 2rem; }
		& img {
			margin-bottom: 1rem;
			max-width: 3.5rem;
			@include rmin($md) {
				max-width: 4rem; } } }
	&__title {
		font-size: 1.125rem;
		font-weight: 700;
		font-family: 'Roboto Condensed';
		color: #ffffff; }
	&__text {
		background: rgba(0,0,0,.7);
		padding: 1.5rem;
		height: 100%;
		color: #ffffff;
		display: flex;
		align-content: center;
		align-items: center;
		opacity: 0;
		visibility: hidden;
		position: relative;
		z-index: 2;
		width: 100%;
		cursor: pointer;
		@include tr(all ease 0.3s);
		@include rmin($md) {
			width: 20rem; } }
	&__item--is-active &__text {
		opacity: 1;
		visibility: visible; }
	&__item {
		&--is-active {
			flex: 0.000000001 0.00000001 100%;
			height: auto;
			@include rmin($md) {
				flex: 0.000000001 0.00000001 20rem; } } }
	&__item--is-active &__block {
		opacity: 0; } }


























// copyright
.copyright {
	padding: 1rem 0;
	background: #2E2423;
	&__wrap {
		display: flex;
		justify-content: space-between; }
	&__left {
		color: #ffffff;
		text-align: left; }
	&__right {
		color: #ffffff;
		text-align: right;
		& a {
			color: #ffffff;
			@include tr(all ease 0.2s);
			&:hover {
				color: $firstColor; } } } }
